import React from 'react'
import ReactDom from 'react-dom'
import styles from './index.module.scss'

let wrap: HTMLElement
export const createMessage = () => (content: React.ReactNode) => {
  if (typeof document === "undefined") {
    return;
  }
  if (!wrap) {
    //å¦‚æœæœ‰çš„è¯ï¼Œè¯´æ˜å·²ç»è°ƒç”¨è¿‡è¿™ä¸ªå‡½æ•°äº†ï¼Œè¿™ä¸ªç©ºdivå°±å¯ä»¥ä¸€ç›´å¤ç”¨
    wrap = document.createElement("div");
    document.body && document.body.appendChild(wrap); //æŒ‚bodyä¸Š
  }
  const divs = document.createElement('div');
  wrap.appendChild(divs);
  ReactDom.render(<Message rootDom={wrap} parentDom={divs} content={content} />, divs)
}

export type MessageProps = {
  rootDom: HTMLElement; //è¿™ä¸ªç”¨æ¥å¹²æ‰parentDom è¿™ä¸ªå¯ä»¥å¸¸é©»
  parentDom: Element | DocumentFragment; //è¿™ä¸ªæ˜¯æŒ‚è½½ç‚¹ è¦unmountå¸è½½ å®Œæ¯•åå¸è½½æŒ‚è½½ç‚¹ æ³¨æ„ï¼ä¸€å…±2æ­¥å¸è½½ï¼Œåˆ«æ¼äº†
  content: React.ReactNode;
};

export function Message(props: MessageProps) {
  const { rootDom, parentDom, content } = props;

  const unmount = React.useMemo(() => () => {
    if (parentDom && rootDom) {
      rootDom.removeChild(parentDom);
    }
  }, [parentDom, rootDom]);

  React.useEffect(() => {
    setTimeout(() => {
      unmount();
    }, 2000);
  }, [unmount]);
  return <div className={styles.Message}>
    <div className={styles.msg}>
      {content}
    </div>
  </div>;
}